<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card   class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18">
                <div class="container">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto ">
                            <div class="">
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Add New Radio Operator Exams</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md">
            <div class="container">
                <div class="row q-col-gutter-md">
                    <div class="col-md-9 col-12 comp-grid">
                        <q-card   class="q-pa-md nice-shadow-18">
                            <div>
                                <ValidationObserver ref="observer" v-slot="{ invalid }" tag="form" @submit.prevent="startSaveRecord()" @reset="resetForm">
                                    <div class="row q-col-gutter-md">
                                        <div class="col-12">
                                            Submitted Application *
                                            <div id="ctrl-submitted_application_id-holder"> 
                                                <api-data-source @loaded="response => mapOptionField(response, 'submitted_application_id')"  api-path="components_data/submitted_application_id_option_list"  v-slot="req">
                                                    <ValidationProvider :rules="{required:true}" name="Submitted Application" v-slot="{ errors, invalid, validated }">
                                                        <q-select   :loading="req.loading"  outlined dense  ref="ctrlsubmitted_application_id" emit-value map-options  v-model="formData.submitted_application_id" :options="req.response" label="Submitted Application"  :error="invalid && validated" :error-message="errors[0]" >
                                                        <template v-slot:option="scope">
                                                            <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                <q-item-section v-if="scope.opt.image" avatar>
                                                                    <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                </q-item-section>
                                                                <q-item-section>
                                                                    <q-item-label />{{ scope.opt.label }}
                                                                        <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                    </q-item-section>
                                                                </q-item>
                                                            </template>
                                                            <template v-slot:selected-item="scope">
                                                                <q-item  v-if="scope.opt">
                                                                    <q-item-section v-if="scope.opt.image" avatar>
                                                                        <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                    </q-item-section>
                                                                    <q-item-section>
                                                                        <q-item-label />{{ scope.opt.label }}
                                                                            <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                        </q-item-section>
                                                                    </q-item>
                                                                </template>
                                                                </q-select> 
                                                            </ValidationProvider>
                                                        </api-data-source>
                                                    </div>
                                                </div>
                                                <div class="col-12">
                                                    Exam Schedule *
                                                    <div id="ctrl-exam_schedule_id-holder"> 
                                                        <api-data-source @loaded="response => mapOptionField(response, 'exam_schedule_id')"  api-path="components_data/exam_schedule_id_option_list"  v-slot="req">
                                                            <ValidationProvider :rules="{required:true}" name="Exam Schedule" v-slot="{ errors, invalid, validated }">
                                                                <q-select   :loading="req.loading"  outlined dense  ref="ctrlexam_schedule_id" emit-value map-options  v-model="formData.exam_schedule_id" :options="req.response" label="Exam Schedule"  :error="invalid && validated" :error-message="errors[0]" >
                                                                <template v-slot:option="scope">
                                                                    <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                        <q-item-section v-if="scope.opt.image" avatar>
                                                                            <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                        </q-item-section>
                                                                        <q-item-section>
                                                                            <q-item-label />{{ scope.opt.label }}
                                                                                <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                            </q-item-section>
                                                                        </q-item>
                                                                    </template>
                                                                    <template v-slot:selected-item="scope">
                                                                        <q-item  v-if="scope.opt">
                                                                            <q-item-section v-if="scope.opt.image" avatar>
                                                                                <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                            </q-item-section>
                                                                            <q-item-section>
                                                                                <q-item-label />{{ scope.opt.label }}
                                                                                    <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                </q-item-section>
                                                                            </q-item>
                                                                        </template>
                                                                        </q-select> 
                                                                    </ValidationProvider>
                                                                </api-data-source>
                                                            </div>
                                                        </div>
                                                        <div class="col-12">
                                                            Radio Operator Exam Type *
                                                            <div id="ctrl-radio_operator_exam_type_id-holder"> 
                                                                <api-data-source @loaded="response => mapOptionField(response, 'radio_operator_exam_type_id')"  api-path="components_data/radio_operator_exam_type_id_option_list"  v-slot="req">
                                                                    <ValidationProvider :rules="{required:true}" name="Radio Operator Exam Type" v-slot="{ errors, invalid, validated }">
                                                                        <q-select   :loading="req.loading"  outlined dense  ref="ctrlradio_operator_exam_type_id" emit-value map-options  v-model="formData.radio_operator_exam_type_id" :options="req.response" label="Radio Operator Exam Type"  :error="invalid && validated" :error-message="errors[0]" >
                                                                        <template v-slot:option="scope">
                                                                            <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                                <q-item-section v-if="scope.opt.image" avatar>
                                                                                    <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                </q-item-section>
                                                                                <q-item-section>
                                                                                    <q-item-label />{{ scope.opt.label }}
                                                                                        <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                    </q-item-section>
                                                                                </q-item>
                                                                            </template>
                                                                            <template v-slot:selected-item="scope">
                                                                                <q-item  v-if="scope.opt">
                                                                                    <q-item-section v-if="scope.opt.image" avatar>
                                                                                        <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                    </q-item-section>
                                                                                    <q-item-section>
                                                                                        <q-item-label />{{ scope.opt.label }}
                                                                                            <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                        </q-item-section>
                                                                                    </q-item>
                                                                                </template>
                                                                                </q-select> 
                                                                            </ValidationProvider>
                                                                        </api-data-source>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div v-if="showSubmitButton" class="text-center q-my-md">
                                                                <q-btn type="submit"    :rounded="false"  color="primary"  no-caps  unelevated   :disabled="invalid" icon="send" :loading="saving">
                                                                    <q-spinner-oval slot="loading" /> Submit
                                                                </q-btn>
                                                            </div>
                                                        </ValidationObserver>
                                                        <slot :submit="submit" :saving="saving"></slot>
                                                    </div>
                                                </q-card>
                                            </div>
                                        </div>
                                    </div>
                                </section>
                            </div>
                        </template>
                        <script>
	import { PageMixin } from "../../mixins/page.js";
	import { AddPageMixin } from "../../mixins/addpage.js";
	import { mapActions } from "vuex";
	export default {
		name: 'addRadiooperatorexamsPage',
		components: {
		},
		mixins: [PageMixin, AddPageMixin ],
		props:{
			pageName : {
				type : String,
				default : 'radio_operator_exams',
			},
			routeName : {
				type : String,
				default : 'radio_operator_examsadd',
			},
			apiPath : {
				type : String,
				default : 'radio_operator_exams/add',
			},
		},
		data() {
            return {
				formData: {
					submitted_application_id: "", exam_schedule_id: "", radio_operator_exam_type_id: "", 
				},
			}
		},
		computed: {
			pageTitle:{
				get: function () {
					return "Add New Radio Operator Exams"
				}
			}
		},
		meta () {
			return {
				title: this.pageTitle
			}
		},
		methods: {
			...mapActions('radio_operator_exams', ['saveRecord']),
			async startSaveRecord(){
				const isFormValid = await this.$refs.observer.validate();
				if(isFormValid){
					this.saving = true;
					let payload = this.normalizedFormData();
					let url = this.apiUrl;
					let data = { url, payload  }
					this.saveRecord(data).then((response) => {
						this.record = response.data;
						this.id = this.record['id'];
						this.saving = false;
						this.resetForm();
						this.closeDialogs();// close page dialog that if opened
						this.flashMsg(this.msgAfterSave);
						this.navigateTo(`/radio_operator_exams`);
					},
					 (response) => {
						this.saving = false;
						this.showPageRequestError(response);
					});
				}
			},
			resetForm (){
				this.formData = {submitted_application_id: "", exam_schedule_id: "", radio_operator_exam_type_id: "", };
				requestAnimationFrame(() => {
					this.$refs.observer.reset();
				});
				this.$EventBus.$emit("resetForm");
			},
		},
		mounted() {
		},
	};
</script>
<style scoped>
</style>
